@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  box-sizing: border-box;
  background-color: #f2f2f2;
}
body {
  display: grid;
  min-width: 320px;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  grid-template-rows: auto 1fr auto;
}
img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}
.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.button {
  display: block;
  width: 284px;
  margin: 0 auto;
  padding: 16px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: inherit;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
}
.button:hover,
.button:focus {
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled,
.button--disabled {
  background-color: #e5e5e5;
}
.header {
  position: relative;
  min-height: 55px;
}
.navigation__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 2;
  display: block;
  width: 185px;
  height: 96px;
  transform: translateX(-50%);
}
.navigation__logo[href="index.html"]:hover {
  opacity: 0.8;
}
.navigation__logo[href="index.html"]:focus {
  opacity: 0.8;
}
.navigation__logo[href="index.html"]:active {
  opacity: 0.3;
}
.navigation__list {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  display: none;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    display: grid;
    background-color: #ffffff;
    grid-template-columns: repeat(4, auto);
  }
}
.navigation__list::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .navigation__list::after {
    display: none;
  }
}
.navigation__item {
  text-align: center;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .navigation__item {
    width: 153px;
    box-shadow: none;
  }
}
@media (min-width: 1200px) {
  .navigation__item {
    width: 239px;
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(4n-2) {
    margin-right: 76px;
  }
}
@media (min-width: 1200px) {
  .navigation__item:nth-child(4n-2) {
    margin-right: 120px;
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(4n-1) {
    margin-left: 76px;
  }
}
@media (min-width: 1200px) {
  .navigation__item:nth-child(4n-1) {
    margin-left: 120px;
  }
}
.navigation__link {
  display: block;
  padding-top: 18px;
  padding-bottom: 12px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .navigation__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
}
@media (max-width: 767px) {
  .navigation__link:hover,
  .navigation__link:focus {
    background-color: #669ec0;
  }
}
@media (min-width: 768px) {
  .navigation__link:hover,
  .navigation__link:focus {
    color: #81b3d2;
  }
}
.navigation__link:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .navigation__link:active {
    background-color: #5496bd;
  }
}
@media (min-width: 768px) {
  .navigation__link:active {
    color: #000000;
  }
}
.navigation__link--current {
  color: #766357;
}
.navigation__btn {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 11;
  width: 23px;
  height: 22px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__btn {
    display: none;
  }
}
.navigation__btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.navigation__btn:hover,
.navigation__btn:focus {
  opacity: 0.6;
}
.navigation__btn:active {
  opacity: 0.3;
}
.navigation--opened .navigation__list {
  display: block;
}
.navigation--opened .navigation__btn::before,
.navigation--opened .navigation__btn::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -1px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.navigation--opened .navigation__btn::before {
  box-shadow: none;
  transform: rotate(45deg);
}
.navigation--opened .navigation__btn::after {
  transform: rotate(-45deg);
}
.navigation--opened .navigation__btn:hover::before,
.navigation--opened .navigation__btn:hover::after {
  opacity: 0.6;
}
.navigation--opened .navigation__btn:active::before,
.navigation--opened .navigation__btn:active::after {
  opacity: 0.3;
}
.no-js .navigation__btn {
  display: none;
}
@media (max-width: 767px) {
  .no-js .navigation__list {
    position: relative;
    display: block;
  }
}
